import './index.html';
import './index.css';

const changeScreenShotImage = (target, nextElement) => {
  const currentImage = target.parentElement.querySelector('.screenshot-image-visible');
  const nextImage = currentImage[nextElement];
  if (nextImage && nextImage.classList.contains('screenshot-image')) {
    currentImage.classList.remove('screenshot-image-visible');
    nextImage.classList.add('screenshot-image-visible');
  }
};

const nextButtonHundler = (event) => {
  if (event.target.classList.contains('screenshot-button-next')) {
    changeScreenShotImage(event.target, 'nextElementSibling');
  } else if (event.target.parentElement.classList.contains('screenshot-button-next')) {
    changeScreenShotImage(event.target.parentElement, 'nextElementSibling');
  }
};

const previousButtonHundler = (event) => {
  if (event.target.classList.contains('screenshot-button-previous')) {
    changeScreenShotImage(event.target, 'previousElementSibling');
  } else if (event.target.parentElement.classList.contains('screenshot-button-previous')) {
    changeScreenShotImage(event.target.parentElement, 'previousElementSibling');
  }
};

window.onload = () => {
  const screenshots = document.querySelector('.home-screenshots');

  screenshots.addEventListener('click', nextButtonHundler);
  screenshots.addEventListener('click', previousButtonHundler);
};
